//PosData
export default class PosDataGh{
    constructor({number,Name,Pos,Speed,Delay,Loop_Time,Is_Loop_Start,Is_Loop_End,Music_State,Music_Number,Is_Cur_Form})
    {
        this.number = number,//
        this.Name = Name,//
        this.Pos = Pos,//
        this.Speed = Speed,//
        this.Delay = Delay,//
        this.Loop_Time = Loop_Time,//
        this.Is_Loop_Start = Is_Loop_Start,//
        this.Is_Loop_End = Is_Loop_End,//
        this.Music_State=Music_State,//01234
        this.Music_Number= Music_Number,// /256 %256
        this.Is_Cur_Form = Is_Cur_Form//0 1
    }
}


export function CreatePosDataDh(posDataDhInit)
{
    return new PosDataGh({
        number : posDataDhInit.number,
        Name : posDataDhInit.Name,
        Pos : posDataDhInit.Pos,
        Speed : posDataDhInit.Speed,
        Delay : posDataDhInit.Delay,
        Loop_Time:posDataDhInit.Loop_Time,
        Is_Loop_Start : posDataDhInit.Is_Loop_Start,
        Is_Loop_End : posDataDhInit.Is_Loop_End,
        Music_State:posDataDhInit.Music_State,
        Music_Number:posDataDhInit.Music_Number,
        Is_Cur_Form:posDataDhInit.Is_Cur_Form

    })
}
